import i18n from 'i18next'
import LanguageDetector from 'i18next-browser-languagedetector'

i18n.use(LanguageDetector).init({
    resources: {
        en: {
            translations: {
                'title': 'Estonian Online',
                'categories': 'Categories',
                'category.lessons': 'Lessons',
                'category.exercises': 'Exercises',
                'category.texts': 'Texts',
                'page.current': '(current)',
                'page.home': 'Home',
                'page.blog': 'Blog',
                'page.about': 'About',
                'lesson': 'Lesson {{number}}',
                'task': 'Task {{number}}',
                'text': 'Text {{number}}',
                'article': 'Article {{number}}'
            }
        },
        ru: {
            translations: {
                'title': ' ',
                'categories': '',
                'category.lessons': '',
                'category.exercises': '',
                'category.texts': '',
                'page.current': '()',
                'page.home': '',
                'page.blog': '',
                'page.about': ' ',
                'lesson': ' {{number}}',
                'task': ' {{number}}',
                'text': ' {{number}}',
                'article': ' {{number}}'
            }
        }
    },
    fallbackLng: 'en',
    debug: true,

    ns: ['translations'],
    defaultNS: 'translations',

    keySeparator: false,

    interpolation: {
        escapeValue: false,
        formatSeparator: ','
    },

    react: {
        wait: true
    },

    detection: {
        order: ['navigator', 'querystring', 'cookie', 'localStorage', 'htmlTag', 'path', 'subdomain']
    }
})

export default i18n



